import React, { useState } from "react";
import buttonWasClicked from './ButtonClick';
import './App.scss';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  useRouteMatch
} from "react-router-dom";
import '../node_modules/font-awesome/css/font-awesome.css';

function NavBar() {
  
  const [isActive,setActive] = useState("home")
  
  return (    
    <nav className="landing-page__nav-bar nav-bar">
    <ul className="nav-bar__list">
    {/* Button to Home Page */}
      <Link to ='/home'><li>
      <button className={` ${isActive === "home" ? 'btn__nav-bar-btn active-link' : 'btn__nav-bar-btn'}`} onClick={() => setActive("home")}>Home</button>
       </li>
      </Link>
    {/* Button to Portfolio Page */}
      <Link to ='/portfolio'><li>
      <button className={` ${isActive === "portfolio" ? 'btn__nav-bar-btn active-link' : 'btn__nav-bar-btn'}`} onClick={() => setActive("portfolio")}>Portfolio</button>
        </li>
        </Link>
    {/* Button to Artwork Page */}
      <Link to ='/artwork'><li>
      <button className={` ${isActive === "artwork" ? 'btn__nav-bar-btn active-link' : 'btn__nav-bar-btn'}`} onClick={() => setActive("artwork")}>Artwork</button>
      </li></Link>
      {/* Button to Photography Page */}
      <Link to ='/photography'><li>
      <button className={` ${isActive === "photography" ? 'btn__nav-bar-btn active-link' : 'btn__nav-bar-btn'}`} onClick={() => setActive("photography")}>Photography</button>
      </li></Link>
      {/* Button to CV Page */}
      <Link to ='/home'><li>
      <button className={` ${isActive === "cv" ? 'btn__nav-bar-btn active-link' : 'btn__nav-bar-btn'}`} onClick={() => setActive("cv")}>CV</button>
      </li></Link>
      {/* Button to About Page */}
      <Link to ='/about'><li>
      <button className={` ${isActive === "about" ? 'btn__nav-bar-btn active-link' : 'btn__nav-bar-btn'}`} onClick={() => setActive("about")}>About</button>
      </li></Link>
      {/* Button to Contact Page */}
      <Link to ='/contact'><li>
      <button className={` ${isActive === "contact" ? 'btn__nav-bar-btn active-link' : 'btn__nav-bar-btn'}`} onClick={() => setActive("contact")}>Contact</button>
      </li></Link>     
          
    </ul>
  </nav>
  
  );
    }
  
export default NavBar;